[v-cloak] {
  display: none;
}

.redirectmate-utility .checkbox-column {
  width: 30px !important;
}

.redirectmate-utility .modal-footer {
  background-color: #f3f7fc;
  padding: 18px;
  box-shadow: 0 0 0 1px #cdd8e4, 0 2px 12px #cdd8e480;
}

.redirectmate-utility .icon.delete:before {
  top: -1px;
}

.redirectmate-utility .icon.search:before {
  position: relative;
  top: -2px;
}

.redirectmate-utility .icon.add:before {
  top: 1px;
}

.redirectmate-utility .status-dot {
  transition-property: transform, opacity;
  transition-duration: .5s;
  transition-timing-function: ease-in-out;
}

.redirectmate-utility .status-dot.is-checking {
  opacity: .5;
  transform: scale(.4);
}

.redirectmate-utility .modal-box-shadow {
  box-shadow: 0 25px 100px #1f293380;
}

